export function TechStackSection() {
  const { ref, isInView } = useInView({ threshold: 0.2 })

  return (
    <section id="techstack" className="py-20 sm:py-32" ref={ref}>
      <div className="container mx-auto px-4 sm:px-6 lg:px-8">
        <div className="max-w-4xl mx-auto">
          <div
            className={`transition-all duration-700 ${
              isInView ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
            }`}
          >
            <h2 className="text-3xl sm:text-4xl font-bold text-foreground mb-4 flex items-center gap-3">
              Tech Stack
              <span className="flex-1 h-px bg-border ml-4 hidden sm:block" />
            </h2>
            <p className="text-muted-foreground text-lg mb-12">Technologies I use to bring ideas to life</p>
          </div>

          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-6">
            {techStack.map((tech, index) => (
              <div
                key={tech.name}
                className={cn(
                  "group flex flex-col items-center justify-center p-6 rounded-xl bg-card border border-border hover:border-primary/50 transition-all duration-300 hover:-translate-y-2 hover:shadow-lg hover:shadow-primary/5",
                  isInView ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8",
                )}
                style={{ transitionDelay: `${(index + 1) * 50}ms` }}
              >
                <div className={cn("transition-transform duration-300 group-hover:scale-110", tech.color)}>
                  {tech.icon}
                </div>
                <span className="mt-3 text-sm font-medium text-muted-foreground group-hover:text-foreground transition-colors">
                  {tech.name}
                </span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}
